.banner {
  display: flex;
  flex-direction: column;
  max-width: 1699px;
  min-height: 688px;
  width: 100vw;
  background: linear-gradient(102.95deg, #00A8CD 62.03%, #047DBF 110.97%);
  color: #fff;
  position: relative;
  padding: 149px 1px 1px 219px;
  gap: 64px;
}

.banner__title {
  font-size: 40px;
  font-weight: 900;
  line-height: 56px;
  letter-spacing: 0.03em;
  align-self: start;
  padding: 0;
  margin: 0;
}

.banner__container {
  display: flex;
  flex-direction: column;
  gap: 59px;
  width: 477px;
  min-height: 156px;
  z-index: 999;
}

.banner__subtitle {
  font-size: 28px;
  font-weight: 400;
  line-height: 39.2px;
  letter-spacing: 0.02em;
  margin: 0;
  padding: 0;
}

.banner__button {
  width: 440px;
  height: 80px;
  padding: 23px 127px 23px 126px;
  border-radius: 8px;
  background-color: #CA0100;
  font-size: 28px;
  font-weight: 400;
  line-height: 33.6px;
  letter-spacing: 0.04em;
  text-align: center;
  border: none;
  color: #FFFFFF;
  transition: .5s;
  cursor: pointer;
}

.banner__img {
  position: absolute;
  top: 0px;
  right: 0px;
}

.banner__button:hover {
  opacity: .6;
}
